@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
body {
  margin: 0;
  font-family: "Space Mono", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}

@media screen and (max-width: 500px) {
  body {
    font-size: 14px;
  }
}

.header {
  position: absolute;
  text-align: center;
  width: 100%;
}

.links {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.links h1 {
  cursor: pointer;
}

.overline {
  text-decoration: overline;
}

.contact {
  line-height: 2em;
}

.contact .about {
  position: relative;
}

.portfolio {
  display: flex;
  flex-direction: row;
  width: 100%;
}

@media screen and (max-width: 699px) {
  .portfolio {
    flex-direction: column;
  }
}

.portfolioLinks {
  flex-direction: column;
  flex: 1 1 282px;
  cursor: pointer;
}
@media screen and (max-width: 500px) {
  .portfolioLinks {
    flex-basis: 220px;
  }
}

.portfolioImages {
  flex: 3 1 282px;
  margin-left: 0.5em;
}

.portfolioDesc {
  display: flex;
  flex-direction: column;
  flex: 1 3 282px;
  margin-left: 5px;
}

.portfolioSpacer {
  flex: 1 1;
}

.portfolioIcon {
  display: flex;
  margin-top: 2em;
  margin-right: 1em;
  justify-content: space-between;
}

.spacer {
  width: 100%;
  height: 7em;
}
@media screen and (max-width: 500px) {
  .spacer {
    height: 6vh;
  }
}

.container {
  margin: 1em 2em;
  display: flex;
}
@media screen and (max-width: 900px) {
  .container {
    margin-left: 0.5em;
  }
}

.resume {
  margin: 2vh 0 0 1vw;
}

h1 {
  font-size: 26px;
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 14px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

